/* add css module styles here (optional) */
._2Dhm6 {
  display: flex;
  flex-direction: row;
  --border: #ccc;
  --row-even: #efefef;
  --row-hover: #ccc;
}

._2Dhm6 table {
  flex-grow: 1;
}

._2Dhm6 ._3sodH { background-color: var(--row-hover); }

._2Dhm6 table { width: 100%; border-collapse: collapse; border-spacing: 0; font-size: 10pt; line-height: 2em; }
._2Dhm6 table td { border-right: 1px solid var(--border); cursor: default; box-sizing: border-box; }
._2Dhm6 table thead th { 
    font-weight: normal; text-align: center; background-color: var(--table-head); 
    border-top: 1px solid var(--border); border-bottom: 1px solid var(--border); 
    border-right: 1px solid var(--border); cursor: pointer;
}
._2Dhm6 table thead th:hover {background-color: var(--row-hover); }
._2Dhm6 table thead td, ._2Dhm6 table tbody td { padding-left: .5em; padding-right: .5em; overflow: hidden; text-overflow: ellipsis; white-space: nowrap; max-width: 0; }
._2Dhm6 table tbody tr:nth-child(even) { background-color: var(--row-even); }
._2Dhm6 table tbody tr._3sodH td { background-color: var(--table-active); }
._2Dhm6 table tbody tr:hover td, table tbody tr._3sodH:hover td { background-color: var(--row-hover); }
._2Dhm6 table tfoot ._3QowC { border-color: var(--border); border-width: 1px 0; border-style: solid; }
._2Dhm6 table tfoot ._3QowC input { border-top: none; border-bottom: none; height: auto; background-color: var(--white); }
